/* JetBrains Mono */

@font-face {
  font-family: "JetBrains Mono";
  font-style: normal;
  font-weight: 400;
  src: url("./JetBrainsMono/JetBrainsMono-Medium.woff") format("woff"),
    url("./JetBrainsMono/JetBrainsMono-Medium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "JetBrains Mono";
  font-style: normal;
  font-weight: 700;
  src: url("./JetBrainsMono/JetBrainsMono-Bold.woff") format("woff"),
    url("./JetBrainsMono/JetBrainsMono-Bold.woff2") format("woff2");
  font-display: swap;
}

@font-face {
  font-family: "Iceland";
  font-style: normal;
  font-weight: 400;
  src: url("./Iceland/Iceland-Regular.woff") format("woff"),
    url("./Iceland/Iceland-Regular.woff2") format("woff2");
  font-display: swap;
}
